import Swiper from 'swiper';

function bgChanger() {
    let currentSlide = document.querySelector('.main-slide.swiper-slide-active');
    let classes = currentSlide.classList;
    for (let i = 0; classes.length > i; i++) {
        if (classes[i] == 'bg_slide__white') {
            document.body.style.backgroundColor = '#E5E5E5';
            let headColor = document.querySelectorAll('.nav');
            document.querySelector('.index_head__name').style.color = '#000000';
            for (let j = 0; j < headColor.length; j++) {
                headColor[j].style.color = '#000000';
            };
        };
        if (classes[i] == 'bg_slide__viol') {
            document.body.style.backgroundColor = '#3E0043';
            let headColor = document.querySelectorAll('.nav');
            document.querySelector('.index_head__name').style.color = '#F8F8F8';
            for (let j = 0; j < headColor.length; j++) {
                headColor[j].style.color = '#F8F8F8';
            };
        };
    }
};

let vh = window.innerHeight;

let swiper = new Swiper('.page-slider', {
    speed: 300,
    parallax: true,
    direction: 'vertical',
    slidesPerView: 1,
    mousewheel: true,
    simulateTouch: false,
    height: (vh)
});

let navButton = document.querySelectorAll('.nav');
navButton.forEach(function(el) {
    el.addEventListener('click', function() {
        swiper.slideTo(el.dataset.slide, 300);
    }, false)
});

swiper.on ("slideChangeTransitionStart", function() {
    bgChanger()
});



